
export class Admin_board_members_View {

	constructor () {
		this.clicked_btn = false;
		this.old_username = '';
	}

	articles_event_listener = (controllerFunction) => {

		const btns = Array.from(
			document.getElementsByClassName("admin_board_members_main_section_article_div_span")
		)

		    btns.forEach((btn, index) => {

		      btn.addEventListener("click", () => {

		        const btn_div = document.getElementById(
		          `admin_board_members_main_section_article_div${index + 1}`
		        );

		        if (this.clicked_btn) {
		          this.clicked_btn = false;
		          btn_div.style.display = "none";
		          btn.textContent = "Click Here";
		          btn.style.fontSize = "110%";
		          btn.style.color = "var(--fourth-color)";
		          btn.style.backgroundColor = "var(--third-color)";

		        } else {
		          this.clicked_btn = true;
		          btn_div.style.display = "flex";
		          btn.textContent = "X";
		          btn.style.fontSize = "200%";
		          btn.style.color = "darkred";
		          controllerFunction(index+1)
		        }

		        const other_divs = Array.from(
		        	document.getElementsByClassName(`admin_board_members_main_section_article_div1`)
		        )

		        other_divs.forEach((div)=>{

		        	if(div.id !== btn_div.id ){

		        		div.style.display = 'none';

		        	}
		        })

		    });

		})
	}

	
	render_fetch_board_members = (state) => {

		const loading_div = 
		document.getElementById("admin_board_members_main_section_article_div1_loading");
		const no_board_members_msg = 
		document.getElementById("admin_board_members_main_section_article_div1_h3");
		const result = 
		document.getElementById("admin_board_members_main_section_article_div1_ul1")


		// loading elements
		if(state.loading){

			no_board_members_msg.style.display = 'none';
			result.style.display = 'none';
			loading_div.style.display = 'none';

			loading_div.style.display = 'block';

		}
		// finished loading, board_members users more than 1 
		else if (state.board_members.length > 0 &&
			state.loading === false) {

			loading_div.style.display = 'none';
			no_board_members_msg.style.display = 'none';
			result.replaceChildren();

			result.style.display = 'flex';

			state.board_members.forEach((user)=>{

				const li = document.createElement("li");

				li.classList.add("admin_board_members_main_section_article_div1_ul1_li");

				li.textContent = user;

				result.appendChild(li)
			})
			
		}
		// finished loading, no board_members 
		else if(state.board_members.length === 0 &&
			state.loading === false) {

			loading_div.style.display = 'none';
			result.style.display = 'none';
			no_board_members_msg.style.display = 'none';

			no_board_members_msg.style.display = 'flex';
		}
		// error just load
		else{
			loading_div.style.display = 'none';
			result.style.display = 'none';
			no_board_members_msg.style.display = 'none';

			no_board_members_msg.style.display = 'flex';
		}
	}

	render_add_board_members = (state) => {

		const loading_div = 
		document.getElementById("add_board_member_form_div_loading");
		const err = 
		document.getElementById("add_board_member_form_err_msg");
		const success = 
		document.getElementById("add_board_member_form_success_msg");

		// loading 
		if(state.loading){

			loading_div.style.display = 'none';
			success.style.display = 'none';
			err.style.display = 'none';

			loading_div.style.display = 'block';

		}
		// board memeber added successfully 
		else if (state.loading === false &&
			state.added_board_member === true) {

			success.style.display = 'none';
			loading_div.style.display = 'none';
			err.style.display = 'none';

			success.style.display = 'flex';
			
		}
		// board memeber not added successfully 
		else if (state.loading === false &&
			state.added_board_member === false &&
			state.attempted_add_board_member === true) {

				success.style.display = 'none';
				loading_div.style.display = 'none';
				err.style.display = 'none';

				err.style.display = 'flex';
		}
	}

	add_board_members =(modelFunction)=>{

		const submit_form = document.getElementById("add_board_member_form");


		submit_form.addEventListener("submit", (event)=>{

			event.preventDefault();

			console.log(event)

			const username = 
			document.getElementById("add_board_member_form_username").value

			const password = 
			document.getElementById("add_board_member_form_password").value

			modelFunction(username,password)
		})
	}

render_edit_board_members = (state) => {
    const loading_div = document.getElementById("admin_board_members_main_section_article_div4_div1_loading");
    const no_members = document.getElementById("admin_board_members_main_section_article_div4_div1_h4");
    const members = document.getElementById("admin_board_members_main_section_article_div4_div1_h42");
    const loading_div2 = document.getElementById("edit_board_member_form_div_loading");
    const err = document.getElementById("edit_board_member_form_err_msg");
    const success = document.getElementById("edit_board_member_form_success_msg");
    const ul = document.getElementById("admin_board_members_main_section_article_div4_div1_ul");

    // Helper to hide everything
    const hideAll = () => {
        loading_div.style.display = 'none';
        no_members.style.display = 'none';
        members.style.display = 'none';
        success.style.display = 'none';
        err.style.display = 'none';
        loading_div2.style.display = 'none';
        ul.replaceChildren();
    }

    // 1️⃣ Loading
    if (state.loading) {
        hideAll();
        loading_div.style.display = 'block';
        loading_div2.style.display = 'block';
    }
    // 2️⃣ Success message after edit
    else if (state.edited_board_member === true) {
        hideAll();
        members.style.display = 'flex';
        success.style.display = 'flex';
        success.innerHTML = `Board Member Username or Password Changed Successfully ✅ Username: ${state.new_edited_user.username}, Password: ${state.new_edited_user.password}`;
    }
    // 3️⃣ Error message if attempted edit failed
    else if (state.edited_board_member === false && state.attempted_edit_board_member === true) {
        hideAll();
        members.style.display = 'flex';
        err.style.display = 'flex';
    }
    // 4️⃣ Board members loaded
    else if (state.board_members.length > 0) {
        hideAll();
        members.style.display = 'flex';

        state.board_members.forEach((bm) => {
            const li = document.createElement("li");
            li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li");
            li.setAttribute("name", bm);
            li.textContent = bm;

            li.addEventListener("click", (event) => {
                this.old_username = event.target.getAttribute("name");

                // Remove 'selected' class from all other LIs
                const allLi = ul.querySelectorAll(".admin_board_members_main_section_article_div4_div1_ul_li");
                allLi.forEach(otherLi => otherLi.classList.remove("admin_board_members_main_section_article_div4_div1_ul_li_selected"));

                // Add 'selected' to clicked li
                li.classList.add("admin_board_members_main_section_article_div4_div1_ul_li_selected");
            });

            ul.appendChild(li);
        });
    }
    // 5️⃣ No board members
    else {
        hideAll();
        no_members.style.display = "flex";
    }
}


	edit_board_members = (modelFunction)=>{

		const submit_form = 
		document.getElementById('edit_board_member_form');

		const username = 
		document.getElementById("edit_board_member_form_username")

		const password = 
		document.getElementById("edit_board_member_form_password")

		submit_form.addEventListener("submit", (event)=>{

			event.preventDefault();

			console.log(this.old_username,username.value,password.value)

			modelFunction(this.old_username,username.value,password.value)
		})
	}
}