

export class Admin_board_members_Model {

	constructor (){

		this.state = {
			loading:false,
			board_members:[],
			added_board_member: false,
			attempted_add_board_member: false,
			edited_board_member: false,
    		attempted_edit_board_member: false,
    		new_edited_user:{
    			username: '',
    			password:''
    		}
		};

		this.listeners = [];
	}

    setState = (newState) =>{ 
    	this.state = {...this.state,...newState};
    	this.notifyListeners();
    	console.log(this.state)
    	return this.state
    }

    registerListener = (listener) => {

    	this.listeners.push(listener)

    	listener(this.state);
    }

    notifyListeners = () => {
    	this.listeners.forEach((listener)=>{
    		listener(this.state)
    	})
    }

    fetch_board_members = () => {

    	this.setState({
			loading:true
    	})

    	fetch("http://localhost:3001/api/admin_board_members_page/fetch_board_members")
    	.then((res)=>{
    		return res.json()
    	})
    	.then((res=>{

    		if(res.success){

    			const usernames = 
    			res.board_members.map((member) =>{
    				return member.username
    			});



    			this.setState({
	    			loading:false,
	    			board_members: usernames
    			})
    		}
    		else{
    			console.log(res.err)
    			this.setState({
	    			loading:false,
	    			board_members:[]
    			})
    		}

    	}))
    	.catch((err)=>{

    		console.log(err)

    		this.setState({
    			loading:false,
    			board_members:[]
    		})
    	})
    }

    add_board_members = (username,password)=>{

    	this.setState({
    		loading:true,
    		added_board_member: false,
    		attempted_add_board_member: false
    	})

		fetch("http://localhost:3001/api/admin_board_members_page/add_board_members", {
		  method: "POST",
		  headers: {
		    "Content-Type": "application/json"
		  },
		  body: JSON.stringify({
		    username: username,
		    password:password
		  })
		})
		.then((res)=>{
			return res.json()
		})
		.then((res)=>{

			if(res.success){
		    	this.setState({
		    		loading:false,
		    		added_board_member: true,
		    		attempted_add_board_member: false
		    	})
			}
			else{
				this.setState({
		    		loading:false,
		    		added_board_member: false,
		    		attempted_add_board_member: true
		    	})
			}
		})
		.catch((err)=>{
				this.setState({
		    		loading:false,
		    		added_board_member: false,
		    		attempted_add_board_member: true
		    	})
		})
    }

    edit_board_members = (old_username,username,password)=>{

    	console.log(old_username,username,password)

    	if(username.length === 0){
    		username === null
    	}
    	else if (password.length === 0) {
    		password === null
    	}

    	this.setState({
    		loading:true,
			edited_board_member: false,
    		attempted_edit_board_member: false
    	})

		fetch("http://localhost:3001/api/admin_board_members_page/edit_board_members", {
		  method: "POST",
		  headers: {
		    "Content-Type": "application/json"
		  },
		  body: JSON.stringify({
		    username: username,
		    password:password,
		    old_username:old_username
		  })
		})
		.then((res)=>{
			return res.json()
		})
		.then((res)=>{

			if(res.success){
		    	this.setState({
		    		loading:false,
					edited_board_member: true,
    				attempted_edit_board_member: false,
    				new_edited_user:{
		    			username: username,
		    			password: password
    				}
		    	})
			}
			else{
				this.setState({
		    		loading:false,
					edited_board_member: false,
		    		attempted_edit_board_member: true
		    	})
			}
		})
		.catch((err)=>{
				this.setState({
		    		loading:false,
					edited_board_member: false,
    				attempted_edit_board_member: true
		    	})
		})
    }



}